import React from 'react';

const NewsLetter = () => {
    return (
        <div id='containerNewsLetter'>
            <h2>NEWSLETTER</h2>
            <p>Join the IWEAR community and be the first to receive the lastest design and event news from IWEAR.</p>
            <div id='ContainerBtnField' >
                <input type='text'name='newsletter' placeholder='Email address'/>
                <button>SUBSCRIBE</button>
            </div>
        </div>
    );
};

export default NewsLetter;