import React from 'react';
import arrival1 from '../assets/arrival1.png'
import arrival2 from '../assets/arrival2.png'
import arrival3 from '../assets/arrival3.png'
import { AiOutlineHeart } from 'react-icons/ai';


const NewArrival = () => {
    return (
        <div id='containerNewArrival'>
            <h2>NEW ARRIVALS</h2>
            <div id='containerProfil'>
                <div className='containerImageText'>
                    <img className='arrivalImage' src={arrival1} alt='model en spécial' />
                    <div className='containerTexte' >
                        <span className='modelArrival' >PO 3785 <br/><span className='prixArrival' >$350</span></span>                        
                        <span className='likeArrival' ><AiOutlineHeart /></span>
                    </div>
                </div>
                <div className='containerImageText'>
                    <img className='arrivalImage' src={arrival2} alt='model en spécial' />
                    <div className='containerTexte'>
                        <span className='modelArrival' >VO 4578 <br/><span className='prixArrival' >$350</span></span>                        
                        <span className='likeArrival' ><AiOutlineHeart /></span>
                    </div>
                </div>
                <div className='containerImageText'>
                    <img className='arrivalImage' src={arrival3} alt='model en spécial' />
                    <div className='containerTexte'>
                        <span className='modelArrival' >ML 1201 <br/><span className='prixArrival' >$350</span></span>                        
                        <span className='likeArrival' ><AiOutlineHeart /></span>
                    </div>
                </div>
                </div>
            
        </div>
    );
};

export default NewArrival;

