import React from 'react';
import vedette1 from '../assets/vedette1.jpg'
import vedette2 from '../assets/vedette2.jpg'
import { AiOutlineHeart } from 'react-icons/ai';

const VedetteHome = () => {
    return (
        <div id='containerVedetteHome'>
            <h2>DISCOVER OUR TOP PICKS</h2>
            <div id='containerVedetteImage'>
                <div id='containervedette1'>
                    <img src={vedette1} alt='lunette soleil promotionelle' />
                    <div className='infoVedette'>
                        <span className='modelVedette' >PO 3785 <br/><span className='prixVedette' >$300</span></span>
                        <span className='likeVedette' activeClassName='allo' ><AiOutlineHeart /></span>
                    </div>
                </div>
                <div id='containervedette2'>
                    <img src={vedette2} alt='lunette promotionelle' />
                    <div className='infoVedette'>
                        <span className='modelVedette' >MO 2548 <br/><span className='prixVedette' >$350</span></span>                        
                        <span className='likeVedette' ><AiOutlineHeart /></span>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default VedetteHome;