import React from 'react';
import homepage from '../assets/homepage.jpg';
import { BsArrowRight } from 'react-icons/bs';

const LunneteHome = () => {
    return (
        <div id='containerLunetteHome'>
         
                <img id='imageHome' src={homepage} alt='lunette' />
         
          
                <div id='containerEnsembleText'>
                    <h2>Timeless best sellers. Autgentic designs.</h2>
                    <p>Unique elegance edged into modern designs</p>
                    <div id='containerBtnTextLunetteHome'>
                        <span id='btnSunglass'>SHOP SUNGLASSES <BsArrowRight className='flecheHome' /></span>
                        <span id='btnEyeglass'>SHOP EYEGLASSES <BsArrowRight className='flecheHome' /></span>
                    </div>
                </div>
          
        </div>
    );
};

export default LunneteHome;


