import React from 'react';
import { NavLink } from 'react-router-dom'
import { AiOutlineHeart, AiOutlineShoppingCart, AiOutlineSearch } from 'react-icons/ai';
import { CgProfile } from 'react-icons/cg';
import { GrMenu } from 'react-icons/gr';

const Navigation = () => {
    return (
        <div id='containerNavigation'>
           <div id='premierNav'>
                <NavLink exact to='/sunglasses'>Sunglasses</NavLink>
                <NavLink exact to='/eyeglasses'>Eyeglasses</NavLink>
                <NavLink exact to='/about'>About</NavLink>
                <NavLink exact to='/blog'>Blog</NavLink>
            </div>
            <div id='mobilNav'>
                <GrMenu />
            </div>
            <div id='deuxiemeNav'>
                <NavLink exact to='/build/index.html'>IWEAR</NavLink>
            </div>
            <div id='iconNav'>                
            <div className='iconHide'><AiOutlineSearch /></div>
            <div className='iconHide'><AiOutlineHeart /></div>
            <div className='iconHide'><CgProfile /></div>
                
                <div id='shopIcon'><AiOutlineShoppingCart /></div>
            </div>
        </div>
    );
};

export default Navigation;





