import React from 'react';
import categorie1 from '../assets/categorie1.jpg'
import categorie2 from '../assets/categorie2.jpg'

const CategorieHome = () => {
    return (
        <div id='containerCategorie' >
            <img className='categorieImage' src={categorie1} alt='categorie pour lunette de vision' />
            <img className='categorieImage' src={categorie2} alt='categorie pour lunette de soleil' />
        </div>
    );
};

export default CategorieHome;